html {
  --navbar-height: 4.5rem;
  --navbar-sm-height: 4rem;
  --navbar-mobile-height: 4rem;
  --sidebar-width: 16rem;
  --sidebar-collapsed-width: 5rem;
  --sidebar-horizontal-height: 4.25rem;
  --sidebar-horizontal-collapsed-height: 4rem;
  direction: ltr;
  text-align: left;
}

html, body {
  height: 100%;
  font-family: "Open Sans", Arial, sans-serif;
}

:focus {
  outline: none;
}

body {
  overflow-x: hidden;
  background-color: #e4e6e9;
  color: #41464d;
}

body:not(.is-document-loaded) * {
  transition: none !important;
}

.body-container {
  background-color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.body-container.container {
  padding: 0;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.05);
}

.body-container:not(.container) {
  max-width: 100vw;
}

.main-container {
  flex: 1 0 auto;
  display: flex;
  position: relative;
  max-width: 100vw;
  background-color: #f2f5f8;
}

.main-content {
  max-width: 100%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .main-content {
    min-height: 100vh;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: calc(100% - 1rem);
  }
}

@media (min-width: 1440px) {
  .container {
    max-width: 1408px;
  }
}

@media (min-width: 1600px) {
  .container.container-plus {
    max-width: 1536px;
  }
}

@media (max-width: 1199.98px) {
  body {
    background-color: #f2f5f8;
  }
  .container {
    max-width: none;
  }
  .body-container {
    max-width: 100vw;
    overflow: hidden;
  }
}

.page-content {
  flex: 1 0 auto;
  padding: 1rem 0.875rem 0.625rem;
}

@media (min-width: 768px) {
  .page-content {
    padding: 1.25rem 1.125rem 0.625rem;
  }
}

@media (min-width: 1200px) {
  .page-content {
    padding: 1.25rem 1.375rem 0.625rem;
  }
}

@media (min-width: 1400px) {
  .page-content {
    padding: 1.25rem 1.75rem 0.625rem;
  }
}

@media (min-width: 1600px) {
  .page-content {
    padding: 1.25rem 2.25rem 0.625rem;
  }
}

@media (min-width: 1640px) {
  .page-content.container:not(.container.container-plus) {
    padding: 1.25rem 1.375rem 0.625rem;
  }
}

@media (min-width: 1840px) {
  .page-content.container-plus {
    padding: 1.25rem 1.375rem 0.625rem;
  }
}

.page-header {
  margin: 0 0;
  padding-bottom: 1rem;
  padding-top: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-header .page-tools {
  align-self: flex-end;
}

.page-title {
  padding: 0;
  margin: 0;
  font-size: 1.75rem;
  font-weight: 400;
}

.page-title .page-info {
  margin: 0 0.25rem;
  font-size: 1.0625rem;
  font-weight: 400;
  display: block;
}

@media (min-width: 768px) {
  .page-header .page-info {
    display: inline-block;
  }
}

@media print {
  .body-container, .main-container, .main-content {
    display: block;
  }
  .sidebar, .navbar {
    display: none;
  }
  .page-content {
    max-width: none !important;
    width: 100% !important;
    min-width: auto !important;
  }
}

